import React, { Component, Suspense } from 'react';
//import Select from 'react-select';
import { toast } from "react-toastify";
import { Accordion, OverlayTrigger, Tooltip } from 'react-bootstrap';
// import { baseUrl } from '../../../../Assets/AssetsHelper';
import PerfectScrollbar from 'react-perfect-scrollbar'
import { Calendar } from 'react-date-range';
import ModernWaterfallTemplate from './Components/ModernWaterfallTemplate';
import ModernSlideshowTemplate from './Components/ModernSlideshowTemplate';
import AtmosphereTemplate from './Components/AtmosphereTemplate';

import { getThemesList, createTemplate } from '../../../../Services/Actions/templateAction'

const ModernCardTemplate = React.lazy(() => import('./Components/ModernCardTemplate'));
const SquarePhotoTemplate = React.lazy(() => import('./Components/SquarePhotoTemplate'));
const HorizontalColumnTemplate = React.lazy(() => import('./Components/HorizontalColumnTemplate'));
const GalleryPhotoTemplate = React.lazy(() => import('./Components/GalleryPhotoTemplate'));
const ColorPicker = React.lazy(() => import('../../CommonComponents/ColorPicker'));


class TemplateAside extends Component {
    state = {
        templateName: "",
        selectedTheme: {},
        selectedThemeData: null,
        templateFormData: null

    }
    onChooseTheme = (theme, selectedThemeData) => event => {
        this.setState({ selectedTheme: theme, selectedThemeData: selectedThemeData })
    }
    onTextTemplateName = event => {
        this.setState({ templateName: event.target.value })
    }
    tempFormData = (data) => {
        this.setState({ templateFormData: data })
    }
    onClickSaveChanges = event => {
        const { templateFormData, templateName, selectedThemeData } = this.state
        if (templateFormData && Object.keys(templateFormData).length > 0) {
            var self = this
            const data = {
                "action": 1,
                "name": templateName,
                "theme": selectedThemeData.themeId,
                "cssStatus": templateFormData.customCss.cssStatus ? 1 : 0,
                "css": templateFormData.customCss.css,
                "backgroundColor": templateFormData.background.color,
                "transparent": templateFormData.background.transparent ? 1 : 0,
                "backgroundImage": templateFormData.background.image ? templateFormData.background.image : "",
                "backgroundImageId": 0,
                "link_font": "Homenaje",
                "css_font": "Homenaje",
                "font_varient": "regular",
                "fontColor": templateFormData.cardStyle.textColor,
                "cardColor": templateFormData.cardStyle.cardColor,
                "postText": 0,
                "postAuthor": templateFormData.advanced.author ? 1 : 0,
                "postTime": templateFormData.advanced.time ? 1 : 0,
                "postFeatured": templateFormData.themeSettings.postPopUp ? 1 : 0,
                "minimumPostWidth": 300,
                "padding": templateFormData.themeSettings.postPadding,
                "scrollDelay": 10,
                "slideDelay": 10,
                "numberOfPosts": templateFormData.themeSettings.numberOfPosts,
                "trimcontent": templateFormData.themeSettings.trimContent ? 1 : 0,
                "playFullVideo": 1,
                "randomizePost": templateFormData.themeSettings.suffleAssets ? 1 : 0,
                "hideUrls": 1,
                "fitRow": templateFormData.themeSettings.bestFit ? 1 : 0,
                "roundEdge": 5,
                "cardType": 1,
                "socialAction": 1,
                "timeForPopup": 10,
                "authorColor": templateFormData.cardStyle.authorColor,
                "iconType": 1,
                "iconColor": templateFormData.cardStyle.socialIconColor,
                "fontSize": parseInt(templateFormData.cardStyle.fontSize),
                "shareOption": 1,
                "hideContent": templateFormData.advanced.hideContent ? 1 : 0,
                "numberOfCoumn": templateFormData.themeSettings.columns && Object.keys(templateFormData.themeSettings.columns).length > 0 ? templateFormData.themeSettings.columns.value : 0,
                "slideDuration": 0,
                "slidePost": 0,
                "autoPlay": 0,
                "popupSlideShow": 0,
                "customUrl": "",
                "showHideOldPost": templateFormData.themeSettings.hideOldAssets.status ? 1 : 0,
                "showHideOlderPost": templateFormData.themeSettings.hideOldAssets.status ? 1 : 0,
                "loadMoreStatus": templateFormData.themeSettings.showmore ? 1 : 0,
                "autoScrollStatus": 0,
                "hidePosts": 0,
                "onsite_settings": "{}"
            }
            createTemplate(data).then((response) => {
                toast.success("Successfully created template")
                self.props.history("/template")
            })
        }
        else {
            toast.error("Please check template data / template name!")
        }

        console.log("templateFormData", templateFormData, templateName, selectedThemeData)
    }
    render() {

        const { themeList, fonts } = this.props
        const { selectedTheme, selectedThemeData, templateName, templateFormData } = this.state
        console.log("templateFormData", this.props)
        return <div className="innerAside flex-shrink-0">
            <div className="card gutter-2b">
                <div className="card-body">
                    <PerfectScrollbar>
                        <div className="innerAsideWrapper">
                            <div className="pb-3">
                                <label className="text-gray-800 mb-2">Template Name</label>
                                <div className="">
                                    <input type="text" name="company" className="form-control form-control-sm" placeholder="Enter Template name..." onChange={this.onTextTemplateName} value={templateName} />
                                </div>
                            </div>
                            <Accordion defaultActiveKey="1">
                                <Accordion.Item eventKey="1" className="pb-3">
                                    <Accordion.Header className="w-100">
                                        <span className="fs-7 fw-semibold">{selectedTheme && selectedTheme.name ? selectedTheme.name : `Choose Theme`}</span>
                                    </Accordion.Header>
                                    <Accordion.Body className="border rounded-1 mt-1 p-2">
                                        {
                                            themeList.map((item) => {
                                                return <div className={`card rounded-1 text-decoration-none cursor-pointer mb-2 ${selectedTheme.themeId == item.themeId ? `active` : ``}`} onClick={this.onChooseTheme({ name: item.name, themeId: item.themeId }, item)}>
                                                    <div className="card-body p-2">
                                                        <div className="d-flex align-items-center">
                                                            <div className="symbol symbol-44 flex-shrink-0 me-2 text-gray-400">
                                                                <svg xmlns="http://www.w3.org/2000/svg" width="61.107" height="61.044" viewBox="0 0 61.107 61.044">
                                                                    <rect width="61" height="61" transform="translate(0.107 0.044)" fill="none" />
                                                                    <path id="Path_2" data-name="Path 2" d="M-917.7-441.956v-31.006h18.8v31.006Zm-22.719,0v-41.665h21.936v41.665Zm-19.586,0v-31.006h18.8v31.006Zm42.305-31.976V-503h18.8v29.068Zm-42.305,0V-503h18.8v29.068Zm19.586-10.658V-503h21.936v18.41Z" transform="translate(960 503)" fill="currentColor" />
                                                                </svg>
                                                            </div>
                                                            <div className="overflow-hidden">
                                                                <p className="fs-8 fw-semibold mb-1 text-gray-dark text-ellipsis">{item.name}</p>
                                                                <p className="fs-9 fw-semibold mb-0 text-gray-600 text-ellipsis">{item.product_type == 1 ? `Widget` : item.product_type == 2 ? `Display` : ``}</p>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>

                                            })
                                        }
                                    </Accordion.Body>
                                </Accordion.Item>
                            </Accordion>
                            {
                                selectedTheme ? <>{selectedTheme.themeId == 4 ? <Suspense fallback={<></>}>
                                    <SquarePhotoTemplate selectedThemeData={selectedThemeData} tempFormData={this.tempFormData} />
                                </Suspense> : selectedTheme.themeId == 21 ? <Suspense fallback={<></>}>
                                    <ModernSlideshowTemplate selectedThemeData={selectedThemeData} tempFormData={this.tempFormData} />
                                </Suspense> : selectedTheme.themeId == 20 ? <Suspense fallback={<></>}>
                                    <ModernCardTemplate selectedThemeData={selectedThemeData} tempFormData={this.tempFormData} />
                                </Suspense> : selectedTheme.themeId == 44 ? <Suspense fallback={<></>}>
                                    <AtmosphereTemplate selectedThemeData={selectedThemeData} tempFormData={this.tempFormData} />
                                </Suspense> : selectedTheme.themeId == 50 ? <Suspense fallback={<></>}>
                                    <GalleryPhotoTemplate selectedThemeData={selectedThemeData} tempFormData={this.tempFormData} />
                                </Suspense> : selectedTheme.themeId == 47 ? <Suspense fallback={<></>}>
                                    <HorizontalColumnTemplate selectedThemeData={selectedThemeData} tempFormData={this.tempFormData} />
                                </Suspense> : selectedTheme.themeId == 58 ? <Suspense fallback={<></>}>
                                    <ModernWaterfallTemplate selectedThemeData={selectedThemeData} tempFormData={this.tempFormData} />
                                </Suspense> : null}</> : null
                            }

                        </div>
                    </PerfectScrollbar>
                    <div className="bg-white pt-2 w-100">
                        <button type="button" className="btn btn-primary w-100" onClick={selectedTheme ? this.onClickSaveChanges : null}>Save Changes</button>
                    </div>
                </div>
            </div>
        </div>
    }
}

export default TemplateAside;